.container {
  display: flex;
  padding-top: 10rem;
  justify-content: center;
  height: auto;
  background-color: #c4161d;
}

.contentContainer {
  display: flex;
  flex: 1;
  align-items: center;
  flex-direction: column;
  min-width: 350px;
}

.title {
  font-size: 2rem;
  color: white;
  margin-bottom: 2rem;
}

.flex1Center {
  display: flex;
  flex: 1;
  justify-content: center;
}

.list {
  margin-bottom: 2rem;
}

@media (max-width: 1140px) {
  .container {
    flex-wrap: wrap;
  }
}
